import React,{useState} from 'react'

import * as FileSaver from 'file-saver';

import * as XLSX from 'xlsx';



const FileReader = ({Date}) => {

  

  const csvData = [

    {id:1,name:'sakumar'},

    {id:2,name:'kumar'}

  ]
  const [dataAll, setdataAll] = useState()
  
  const getUserList = () => {
      if (Date == "1 ") {
        return fetch('http://my-json-server.typicode.com/Artem9989/Mock/all')
        .then(res =>  res.json());
      } else if (Date == "5 ") {
        return fetch('http://my-json-server.typicode.com/Artem9989/Mock/allTwo')
        .then(res =>  res.json());
      } 
      else {
        return fetch('http://my-json-server.typicode.com/Artem9989/Mock/allThree')
        .then(res =>  res.json());
      }
   
  }
 const getUserBD = () => {
   
        return fetch('http://my-json-server.typicode.com/Artem9989/Mock/db')
        .then(res =>  res.json());
  
 }
  
    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';

    const fileExtension = '.xlsx';



    const exportToCSV = async () => {
        const data = await getUserList();
        await setdataAll(data)
        // console.log(data[1])
        let fileName = `  ${Date}`
        //   setTimeout(() => {
        //     csvLinkEl.current.link.click();
        //   });

        const ws = XLSX.utils.json_to_sheet(data);

        const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };

        const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });

        const dataTwo = new Blob([excelBuffer], {type: fileType});

        FileSaver.saveAs(dataTwo, fileName + fileExtension);

    }

    const exportTo = async () => {
        const data = await getUserBD();
        await setdataAll(data)
        console.log(data)
        // console.log(data[1])
        let fileName = `   `
        //   setTimeout(() => {
        //     csvLinkEl.current.link.click();
        //   });
        const wsAll = data["all"].concat(data["allTwo"].concat(data["allThree"]))
        const ws = XLSX.utils.json_to_sheet(
            wsAll       
        );
        
        // const wsTwo = XLSX.utils.json_to_sheet(data["allTwo"]);
        // const wsThree = XLSX.utils.json_to_sheet(data["allThree"]);
        

        const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
        
        const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });

        const dataTwo = new Blob([excelBuffer], {type: fileType});

        FileSaver.saveAs(dataTwo, fileName + fileExtension);

    }

    return (
<div className="btn_export">

        <button  onClick={(e) => exportToCSV(csvData)}> </button>
      
        <button  onClick={(e) => exportTo(csvData)}>  </button>
      
</div>
    )

}
export default FileReader;