import React, {  useEffect, useState } from 'react';
// import './AdminAuth.module.css'
import {  CarOutlined } from '@ant-design/icons';
// import addDriver from './AddDriver/addDriver.jsx';
// import { Field, reduxForm } from "redux-form";
import 'antd/lib/button/style/index.css';
import { Form, Input, Button, Modal } from 'antd';


const AddDriverComponent = (props) => {
  const { modalText,setModalText, setVisible, addDrivers, driverId,  visible } = props
  // const dispatch = useDispatch();

  // console.log(addDrivers);
  // console.log(`testL` + driverId);

  // const Roles = [{ nameRole: 'admin', nameRoleRu: '', idRole: '90f84438-6762-4394-887a-c3196f7e5877' },
  // { nameRole: 'guest', nameRoleRu: '', idRole: '18a0659d-df47-4c49-b727-0264b81e83e0' },
  // { nameRole: 'driver', nameRoleRu: '', idRole: 'e0528690-e98f-480b-ab63-e0d9b81b2b11' },
  // { nameRole: 'manager', nameRoleRu: '', idRole: '5d72b87b-605e-4429-ac11-b29ea5aeed40' },
  // { nameRole: 'Who?', nameRoleRu: '', idRole: '00000000-0000-0000-0000-000000000000' },

  // ];


  const [form] = Form.useForm();
  const [, forceUpdate] = useState({});
  useEffect(() => {
    forceUpdate({});
  }, []);
  // function setRole () {
  //   let numberRole = 0
  //   for( numberRole = 0; numberRole<Role.length; numberRole++)
  //     {
  //       return Role[numberRole].idRole
  //     }
  //   // return Role[numberRole].nameRole
  // }


  //  let state = { visible: false };

  // console.log(modalText)
  const [enterLoading, setenterLoading] = useState(false)
  const handleOk = (value) => {
    addDrivers(driverId, value.vehicleNumber, value.vehicleType )
    // console.log(driverId)
    setenterLoading(true)
    // console.log(e);
    setVisible(false);
    setModalText(' ');
    setConfirmLoading(true);
    setTimeout(() => {
      setVisible(false);
      setConfirmLoading(false);
      setenterLoading(false)
      setModalText('    ')
      form.resetFields();
    }, 2000);

  };
  const handleCancel = (e) => {
    console.log(e);
    setVisible(false)
    form.resetFields();
  };
  //  
  // const [visible, setVisible] = useState(false);
  const [confirmLoading, setConfirmLoading] = useState(false);

//    ,     addDriver
  setModalText(`        ID ${driverId}`)

  return <Modal
    title={modalText}
    visible={visible}
    confirmLoading={confirmLoading}
    onCancel={handleCancel}
    footer={null}
    
  >


    <Form onFinish={handleOk} form={form} name="horizontal_login" layout="inline" >
      <Form.Item
        style={{ width: '100%', marginTop: 10, marginBottom: 20 }}
        name="vehicleNumber"
        label=" "

        rules={[
          {
            required: true,
            message: ',   ',
          },
        ]}
      >
        <Input prefix={<CarOutlined className="site-form-item-icon" />} placeholder="  (102102)" />
      </Form.Item>
      <Form.Item
        style={{ width: '100%', marginTop: 10, marginBottom: 20 }}
        name="vehicleType"
        label="  "
        rules={[
          {
            required: true,
            message: ',   ',
          },
        ]}
      >
        <Input prefix={<CarOutlined className="site-form-item-icon" />} placeholder="  ()" />
      </Form.Item>
      <Form.Item shouldUpdate={true} style={{ marginLeft: 200, marginTop: 20 }} >
        {() => (
          <Button
            type="primary"
            htmlType="submit"
            loading={enterLoading}
            
            disabled={
              !form.isFieldsTouched(true) ||
              !!form.getFieldsError().filter(({ errors }) => errors.length).length
            }
          >
            
          </Button>
        )}
      </Form.Item>
    </Form>

  </Modal>


}

// export default connect(withAuthRedirect)(AdminPanel);

export default AddDriverComponent
