import React, { memo } from 'react'
import { Link } from 'react-router-dom'
import { Result, Button } from 'antd';
import './NotFound.css';


const NotFound = () => {


    return <>
        <div className="container">
            <div className="row">

            <Result 
                status="404"
                title="404"
                subTitle=" ,     ."
                extra={<Button type="primary"> <Link to="/login"></Link></Button>}
            />
            </div>
        </div>
    </>

}
export default memo(NotFound)